.Container{
    width:100%;
    justify-self: center;
    text-align:center;
}
.buttonsWrapper{
    padding-top:20px;
    justify-content:space-between;
    text-align:center;
    .typebuttons{
        margin:3px;
        padding:3px 0px;
        width:15%;
        color:rgb(27, 27, 27);
        font-size:13px;
        font-weight:600;
        display:inline-block;
        border-bottom-width:2px;
        border-bottom-color:red;
        border-radius: 3px;
        background-color:white;
    }
    .selectText{
        color:rgb(27, 27, 27);
        font-size:18px;
        font-weight:600;
        background-color:rgb(238, 238, 238);
        padding : 10px 0px;
        width:100%;
    }
}
.searchButton{
    background-color:green;
    color:black;
    margin-left:10;
}

.servicesWrapper{
    display: flex;
    justify-content: center;
}
.searchInput{
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom-width: 2px;
    border-bottom-color: black;
    background-color: rgba(255, 255, 255, 0);
    height:35px;
    margin-top:10px;
    width:50%;
}