@import '~antd/dist/antd.css';

@font-face {
  font-family: 'font_HBS';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'payboocbold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Bold.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'paybooclight';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

$border: #85858559;

.menu {
  border-bottom: solid 1px $border;
  position: fixed;
  /* box-shadow: 0 0 30px #f3f1f1; */
  width: 100%;
  background-color: #ffffff;
  text-align: center;
  height:55px;
  display:flex;
  padding-top:6px;
  z-index:12;
  justify-content: space-between;
  div{
    display:flex;
    align-items: center;
    width:50%;
  }
  .menu__logo a {
    display: inline-block;
    font-size: 27px;
    color:rgb(173, 20, 115);
    font-family: 'font_HBS';
    margin-left:10%;
  }
  .links{
    display:flex;
    justify-content: center;
  }
  .menu__container {
    justify-content:flex-start;
    margin-left:5%;
  }
}


.menu__container a:hover{
  color:rgb(105, 105, 105);
}

.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: #d9dadb;
}

.navButton{
  margin:0px;
  margin-left:15px;
  color:rgb(0, 0, 0);
  font-family:'payboocbold';
  font-size:13px;
}

