
@font-face {
    font-family: 'font_HBS';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'payboocbold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Bold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'paybooclight';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Light.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

.walkydoggy{
    display:flex;
    justify-content: center;
    align-items: center;
    background-color:green;
    width:100%;
    height:70%;   
    .textpart{
        text-align: left;
        padding-top:0%;
        margin-left:16%;
        width:100%;
    } 
    .title{
        font-size:40px;
        font-weight: 900;
        color:yellow;
        font-family: 'payboocbold';
        margin-bottom:20px;
    }
    .imagepart{
        padding-left:5%;
        width:90%;
        padding-top:5%;
        .image{
            width:500px;
        }
    }
    .title2{
        font-size:20px;
        font-weight: bold;
        color:white;
        font-family: 'payboocbold';
    }
    .desc{
        font-size:15px;
        font-weight: bold;
        color:white;
        font-family: 'payboocbold';
    }
    .howmany{
        font-size:17px;
        font-weight: bold;
        color:white;
        font-family: 'payboocbold';
        .number{
            color:orange;
        }
    }
    .require{
        font-size:17px;
        font-weight: bold;
        color:white;
        font-family: 'payboocbold';
    }
    .inputData{
        width:60%;
        height:40px;
    }
    .inputButton{
        width:30%;
        margin-bottom:5px;
        margin-left:5px;
        background-color:rgba(135, 207, 235, 0);
        color:black;
        text-align:center;
        border: 1px solid black;
        &:hover{
            background-color:green;
        }
    }
    .newstable{
        margin-top:10px;
        padding:5px;
        width:500px;
        font-family: 'payboocbold';
        background-color:rgba(88, 88, 88, 0.774);
        .news{
            color:white;
            margin-bottom: 5px;
        }
    }
}

@media (max-width: 700px) {
    .walkydoggy{
        flex-direction:column;
        .textpart{
            text-align:center;
            margin-left:0px;
            width:90%;
        }
        .imagepart{
            width:80%;
            padding-left:0%;
            text-align:center;
            .image{
                width:95%;
            }
        }
        .newstable{
            margin-top:10px;
            margin-left:30px;
            padding:5px;
            width:90%;
            font-family: 'payboocbold';
            background-color:rgba(88, 88, 88, 0.774);
            .news{
                color:white;
                margin-bottom: 5px;
                font-size:12px;
            }
        }
    }
}