@import '~antd/dist/antd.css';

@font-face {
  font-family: 'font_HBS';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'payboocbold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Bold.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'paybooclight';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/paybooc-Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

.container{
    width: 700px; 
    display: flex;
    flex-direction: column;
    margin:10;
    // border: 1px solid rgb(253, 126, 126);
    border-radius: 30px;
    border:1px solid #A8D1E5;
    color:rgb(27, 27, 27);
    background-color: white;
    box-shadow: 10px 10px 10px rgba(25,25,25,0.2);
    padding-top:10px;
    div{
        // border:1px black solid
    }
    font-family: 'paybooclight';
}
.d_top{
    display:flex;
    width:100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    flex-direction: column;
    align-items: center;
    .d_left{
        display:table; 
        width:100%;
        flex-direction: row;
        margin-top:0%;
        div{
            display:table-cell;
            justify-content: center;
            width:33%;
        }
        .move_button{
            font-size:16px;
            text-decoration: none;
            color:black;
        }
    }
}

.d_right{
    border-top-left-radius: 10px;
    width:80%;
    .infos{
        display: flex;
        width:100%;
        p{
            text-align:center;
        }
        span{
            display: block;
            justify-content: center;
            align-items: center;
            width:33%;
            .info_title{
                padding-top:7px;
                border: 2px solid rgb(243, 243, 243);
                vertical-align:middle;
                font-size:1.1em;
                text-align: center;
                background-color: #a8d1e5c4;
                height:40px;
            }
            .info_desc{
                font-size:1.2em;
                font-weight: 700;
            }
        }
    }
    .title{
        align-items: center;
        font-size:10px;
        p{
            padding-top:1%;
            font-size:3em;
            font-weight: 600;
        }
    }
    .oneline{
        font-size:1.0em;
        display:inline-flex;
        align-items: center;
        padding:0px 20px;
        flex-direction: column;
        .func{
            width:100%;
            text-align: left;
        }
    }
    .description{
        display:flex;
        align-items: center;
        padding-top:10px;
        padding-bottom:10px;
    }
}
.reviewWrapper{
    flex-direction: column;
    .rv{
        justify-content: center;
        align-items: center;
        width:100%;
        border-top: 1px solid #A8D1E5;
        background-color:#a8d1e550;
        padding-top:3%;
        color:rgb(27, 27, 27);
        .reviewNum{
            font-size:1.1em;
            font-family: 'payboocbold';
        }
        p{
            font-size:15px;
            text-align:left;
            padding-left:10px;
        }
        div{
            display:block;
            p{
                width: 100%;
                margin-right: 5%;
                // border:1px solid black;
            }
        }
    }
}

.d_foot{
    background-color: #A8D1E5;
    height:40px;
    // border-bottom-right-radius: 10px;
    // border-bottom-left-radius: 10px;
    display: inline-flex;
    width:100%;
    align-items: center;
    p{
        margin-left:3%;
        margin-top:2%;
        color: black;
        font-weight: bold;
        font-size:0.9em;
        background-color:#5bb7e6;
        padding:0 10px;
        border-radius:3px;
    }
}

@media (max-width: 767px) {
    .container{
        width: 95%; 
        display: flex;
        flex-direction: column;
    }
    .d_top{
        display:flex;
        width:100%;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        flex-direction: column;
        align-items: center;
        .d_left{
            display:table; 
            width:100%;
            flex-direction: row;
            margin-top:0%;
            div{
                display:table-cell;
                justify-content: center;
                width:33%;
            }
            .move_button{
                font-size:15px;
                text-decoration: none;
                color:black;
            }
        }
    }
    .d_right{
        border-top-left-radius: 10px;
        width:100%;
        flex-direction: column;
        .infos{
            display: flex;
            width:100%;
            span{
                display: block;
                justify-content: center;
                align-items: center;
                width:33%;
                // border: 1px solid green;
                .info_desc{
                    font-size:1.2em;
                    font-weight: 700;
                }
            }
        }
        .title{
            font-size:7px;
        }
        .description{
            display:flex;
            align-items: center;
        }
    }

    .reviews{
        // width:100%;
        // display:flex;
        // justify-content: center;
        // height: 30%;
        flex-direction:column;
        display:flex;
        .reviewWrapper{
            flex-direction: column;
            .rv{
                justify-content: center;
                align-items: center;
                width:100%;
                border-top: 1px solid #A8D1E5;
                background-color:#a8d1e550;
                padding-top:3%;
                color:rgb(27, 27, 27);
                .reviewNum{
                    font-size:1.1em;
                    font-family: 'payboocbold';
                }
                p{
                    font-size:13px;
                    text-align:left;
                    padding-left:10px;
                }
                div{
                    display:block;
                    p{
                        width: 100%;
                        margin-right: 5%;
                        // border:1px solid black;
                    }
                }
            }
        }
    }
}
