
@font-face {
    font-family: 'font_HBS';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}  
@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

$text: rgba(27,27,27,1);

.app{
    height:300%;
    display:block;
    justify-content: center;
    align-items: center;
}

.all_container{
    display:flex;
    justify-content: center;
}
.all_container2{
    width:600px;
    text-align:center;
    .typebuttons{
        width: 23%;
        height: 50px;
        margin: 5px;
        border-radius:0px;
        color:$text;
    }
    .selectDescText{
        display:flex;
        justify-content: center;
        align-items: center;
        margin-top: 1%;
    }
    .selectText{
        font-size:23px;
        font-family: 'payboocbold';
        text-align:center;
        font-weight:'100';
    }
    .selectDescText2{
        padding:10px;
        background-color: rgba(99, 99, 99, 0.1);
    }
}
.secondContainer{
    justify-content: center;
    text-align:center;
}
.questionWrapper{
    display:inline-block;
    justify-content: center;
    text-align:center;
    width:600px;
    .questionButtons{
        font-size:15px;
        font-weight:550;
        height:100px;
        margin:10px;
        width:45%;
        // border:2px solid #A8D1E5;
        color:$text;
        font-family: 'Pretendard-Regular';
        background-color:rgba(85, 85, 85, 0.267);
    }
    .reloadButton{
        background-color:#009255;
        height:40px;
        width:100px;
        margin-top:20px;
        margin-bottom:10px;
    }
}
.resultButton{
    background-color:blue;
    width:300;
    height:150;
}
.resultWrapper{
    margin-top:-400px;
}

.typeQuestion{
    margin-top:12px;
    font-size:20px;
    font-family: 'payboocbold';
    text-align:center;
    font-weight:'100';
    display:flex;
    align-items: center;
    justify-content: center;
    color:$text;
}
.typeQuestion2{
    font-size:13px;
    font-family: 'payboocbold';
    text-align:center;
    display:flex;
    margin-top:-12px;
    align-items: center;
    justify-content: center;
    color:rgba(27,27,27,0.7);
}

.showResultButton{
    width:300px;
    height:100px;
    background-color:rgba(206, 56, 56, 0.801);
    font-size:23px;
}

@media (max-width: 767px) {
    .all_container2{
        width:100%;
        text-align:center;
        .typebuttons{
            width: 21%;
            height: 50px;
            margin: 5px;
            border-radius:0px;
            color:$text;
        }
    }
    .questionWrapper{
        display:inline-block;
        justify-content: center;
        text-align:center;
        width:95%;
        .questionButtons{
            align-items:center;
            font-size:15px;
            height:70px;
            margin:10px;
            padding-top:23px;
            width:90%;
            // border:2px solid #A8D1E5;
            color:$text;
            font-family: 'Pretendard-Regular';
        }
    }

    .selectText{
        font-size:15px;
    }    
    .selectText2{
        font-size:18px;
    }    
    .showResultButton{
        width:300px;
        height:100px;
        background-color:rgba(0, 0, 0, 0.801);
        font-size:23px;
    }
}