
@font-face {
    font-family: 'font_HBS';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}  

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}
.main_title{
    display: block;
    font-size: 43px;
    color:rgb(34, 34, 34);
    font-family: 'font_HBS';
}

.container2{
    width: 100%;
    flex-direction: row;
    padding-top:50px;
    display: flex;
    justify-content:center;
}

.container3{
    width:600px;
    // background-color:rgba(2, 151, 177, 1);
    z-index:1;
    border: 3px solid #009255;
    border-radius:7px;
    justify-content: center;
    display: block;
    padding-bottom:30px;
    padding-top:30px;
    color:rgb(31, 31, 31);
    .main_title{
        width:100%;
        // display: flex;
        font-size: 26px;
        color:rgb(14, 14, 14);
        justify-content: center;
        align-items: center;
        text-align:center;
        font-family: 'Pretendard-Regular';
        font-weight: bold;
    }
    .main_title_big{
        width:100%;
        // display: flex;
        font-size: 50px;
        color:#009255;
        font-family: 'font_HBS';
        justify-content: center;
        align-items: center;
        text-align:center;
    }
    .sub_desc{
        font-size:16px;
        color:rgb(27, 27, 27);
        width:100%;
        display: flex;
        flex-direction:column;
        justify-content: center;
        align-items: center;
        text-align:center;
        p{
            line-height: 27px;
        }
        .Tip{
            background-color:rgba(245, 117, 196, 0.719);
            color:rgb(173, 20, 115);
            font-weight:bold;
            width:50px;
            border-radius:10px;
        }
    }  
    .downButton{
        background-color:rgb(173, 20, 115);
        width:180px;
        height:70px;
        border-radius:40px;
        margin-top:40px;
        display: table;
        align-items: center;
        justify-content: center;
        &:hover{
            background-color:rgba(219, 26, 145, 0.719);
        }
    }  
    .downDiv{
        display: flex;
        justify-content: center;
        font-size:13px;
    }
    .downArrow{
        display:table-cell;
        vertical-align:middle;
        text-align:center;
        color:white;
        font-size:1.3em;
    }
    
}

.image{
    width:600px;
    margin-right:200px;
}

@media (max-width: 700px) {
    .container3{
        width:95%;

        .main_title_big{
            width:100%;
            // display: flex;
            font-size: 40px;
            font-family: 'font_HBS';
            justify-content: center;
            align-items: center;
            text-align:center;
        }

        .main_title{
            width:100%;
            // display: flex;
            font-size: 23px;
            font-family: 'font_HBS';
            justify-content: center;
            align-items: center;
            text-align:center;
        }
        .sub_desc{
            width:100%;
            // display: flex; 
            font-size:15px;
            font-family: 'Pretendard-Regular';
            justify-content: center;
            align-items: center;
            text-align:center;
            margin-top:20px;
        }
    }
    .downButton{
        background-color:rgb(219, 26, 145);
        width:120px;
        height:70px;
        border-radius:30px;
        margin-top:70;
        &:hover{
            background-color:rgba(219, 26, 145, 0.719);
        }
    }
}