.uploadcontainer{
    text-align: center;
    justify-content:center;
    align-items: center;
    display: flex;
    align-content: center;
    background-color:rgba(27,27,27,1);
    color:rgb(226, 226, 226);
    height:1000px;
    margin-top:-60px;
}
.uploadIncontainer{
    margin-top:-300px;
    width:600px;
    border:1px solid #009255;
    border-radius: 8px;
    height:600px;
    display: flex;
    // justify-content: center;
    align-items: center;
    padding-left:5%;
    .thanks{
        font-size:24px;
        font-weight:600;
        display: flex;
        color:#009255;
    }
    .nameInput{
        background-color:rgba(255,255,255,0);
        border-bottom-color:#9e9e9e;
        border-bottom-width: 2px;
        border-top-width: 0px;
        border-left-width: 0px;
        border-right-width: 0px;
        height:50px;
        display: flex;
        font-size:14px;
        margin-top:-10px;
        color:rgb(226, 226, 226);
    }
    .inputLabel{
        font-size:16px;
        font-weight:600;
        display: flex;
        margin-top:10%;
    }
    .inputButton{
        width:100px;
        height:40px;
        font-size: 15px;
        font-weight: bold;
        margin-top:50px;
        color: rgba(226, 226,226,1);
        background-color:#009255;
    }
}

@media (max-width: 700px) {
    .uploadIncontainer{
        width:95%;
    }
}